import { Component, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { IOptionsPsComplexLookUpComponentExposed } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-lookup/ps-complex-lookup.component.interfaces';
import { PsOptionCardComponent } from 'src/app/commonBussinessSRC/psComponents/ps-container-lookup-option/ps-option-card/ps-option-card.component';
import { IOmniCardItem } from 'src/app/commonBussinessSRC/psComponents/ps-container-lookup-option/ps-option-card/ps-option-card.component.interface';
import { CardsService } from 'src/app/commonBussinessSRC/psServices/Cards/cards.service';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { IOmniCardsRequest } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-interfaces';
import { PsBaseFieldComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-base-field.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { IMapKeyValue } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { IOptionsPsLookupPrepaidCardsExposed } from './ps-lookup-prepaid-cards.component.interface';

@Component({
  selector: 'ps-lookup-prepaid-cards',
  templateUrl: './ps-lookup-prepaid-cards.component.html',
  styleUrls: ['./ps-lookup-prepaid-cards.component.scss']
})
export class PsLookupPrepaidCardsComponent extends PsBaseFieldComponent implements OnInit {

  public formGroup: FormGroup = new FormGroup({});
  @Input() options: IOptionsPsLookupPrepaidCardsExposed = {
    requestObject: null
  };
  public complexLookupOptions: IOptionsPsComplexLookUpComponentExposed = {
    requestObject: null,
    notFoundMessage: 'no_prepaid_card_found_key'
  };
  // eslint-disable-next-line @angular-eslint/no-output-on-prefix
  @Output() public onPrepaidCardChange: EventEmitter<any> = new EventEmitter<any>();
  @ViewChild('complexLookupComponentObj') complexLookupComponentObj;
  public labelValuesMap = new Map<string, IMapKeyValue>();
  public prepaidCardRequestParam: IOmniCardsRequest = {};

  constructor(
    public commonService: PsCommonService,
    logger: LoggerService,
    private crdsSrvce :CardsService
  ) {
    super(commonService, logger);
  }

  ngOnInit() {
    super.init();
    this.loadPrepaidCards();
    this.complexLookupOptions.title = 'title_key';
    this.complexLookupOptions.subTitle = 'sub_title_key';
    this.complexLookupOptions.labelsValueMap = this.labelValuesMap;
    this.commonProv.copyObject(this.complexLookupOptions, this.options, false);
    this.complexLookupOptions.requestObject = this.options.requestObject;
    if (this.options && this.options.component) {
      this.complexLookupOptions.component = this.options.component;
    } else {
      this.complexLookupOptions.component = PsOptionCardComponent;
    }
  }

  /**
   * populating prepaid card
   */
  private loadPrepaidCards() {
    this.prepaidCardRequestParam = {};
    const cifInfo = this.commonProv.session.getValueOf(ConstantCommon.USERINFO);
    this.prepaidCardRequestParam.vsBranchCode = cifInfo.cifBranch;
    this.prepaidCardRequestParam.cardType = 'Prepaid';
    this.returnPrepaidCards(this.prepaidCardRequestParam);
  }

  /**
   * fetching prepaid cards from server
   * @param requestData IOmniBeneficiaryRequest
   */
  private async returnPrepaidCards(requestData: IOmniCardsRequest) {
    const result = await this.crdsSrvce.returnCardList(requestData).catch(error => {
      this.complexLookupOptions.listOfOptions = [];
      this.logger.error('Error: While fetching prepaid cards in PsLookupPrepaidCardsComponent :', error);
    });

    if (result && result.gridModel.length > 0) {
      this.complexLookupOptions.listOfOptions = this.populatePrepaidCards(result.gridModel);
    } else {
      this.complexLookupOptions.listOfOptions = [];
    }
  }

  onChangeItem(cardItm) {
    this.onPrepaidCardChange.emit(cardItm); ////Add by Mohanad AZDB-5369-22072024
  }

  // setDefaultValue(value) {
  //   this.complexLookupRef.popListUp(null);
  //   this.complexLookupRef.onChangeItem(value);
  // }

  /**
   * customizing prepaid card object
   * @param gridModel
   */
  private populatePrepaidCards(prepaidCards: Array<IOmniCardItem>): IOmniCardItem[] {
    return this.crdsSrvce.populateCards(prepaidCards,CardsService.PREPAID_CARD_TYPE_NAME);
    
  }

  reset() {
    this.complexLookupComponentObj.resetLookUp();
  }


}

